.contact__container{
    grid-template-columns: repeat(2, max-content);
    justify-content: center;
    column-gap: 6rem;
    padding-top: 3rem;
    padding-bottom: 3rem;
    margin-top: 1rem;
}

.contact__title{
    position: relative;
    font-size: var(--h3-font-size);
    font-weight: var(--font-medium);
    margin-bottom: var(--mb-1-5);
}

.contact__title::before{
    position: absolute;
    background: var(--title-color-dark);
    height: 2px;
    width: 100px;
    left: 0;
}

.contact__info{
    display: grid;
    row-gap: 2.75rem;
    grid-template-columns: 200px;
}

.contact__card{
    padding: 1rem 0;
    text-align: center;
    background: var(--title-color);
    border-radius: .75rem;
}

.contact__card-icon{
    font-size: 2rem;
    color: var(--title-color-dark);
    margin-bottom: var(--mb-0-25);
}

.contact__card-title, .contact__card-data{
    font-size: var(--small-font-size);
}

.contact__card-title{
    font-weight: var(--font-medium);
}

.font__card-data{
    display: block;
    margin-bottom: var(--mb-0-75);
}

.contact__button{
    color: var(--title-color-dark);
    font-size: var(--small-font-size);
    display: flex;
    align-items: center;
    justify-content: center;
    column-gap: 0.25rem;
}

.contact__button-icon{
    font-size: 1rem;
    transition: 0.3s;
}

.contact__button:hover .contact__button-icon{
    transform: translate(0.25rem);
}

.contact__form{
    width: 500px;
}

.contact__form-div{
    position: relative;
    margin-bottom: var(--mb-2);
    height: 4rem;
}

.contact__form-input{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 2px solid rgba(0, 0, 0, 0.2);
    border-radius: .75rem;
    background: none;
    color: var(--text-color);
    outline: none;
    padding: 1.5rem;
    z-index: 1;
}

.contact__form-tag{
    position: absolute;
    top: -0.75rem;
    left: 1.25rem;
    font-size: var(--smaller-font-size);
    padding: 0.25rem;
    background: var(--body-color);
    z-index: 10;
}

.contact__form-area{
    height: 11rem;
}

.contact__form-area textarea{
    resize: none;
}

/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .contact__container{
        column-gap: 3rem;
    }
}

/* For medium devices */
@media screen and (max-width: 768px) {
    .contact__container{
        grid-template-columns: 1fr;
        row-gap: 3rem;
    }

    .contact__info{
        grid-template-columns: 1fr;
        justify-content: center;
    }

    .contact__from{
        margin: 0 auto;
    }
}

@media screen and (max-width: 576px) {
    /* .contact__info{
        grid-template-columns: 1fr;
    } */

    .contact__form{
        width: 100%;
    }
}

